/*
	We do not recommend changing the parameters
	to keep the look and feel consistent
*/

#MeteorToys_throttle {
	height: 46px !important; /* maximum 600px */
	width: 46px !important;  /* maximum 600px */
	/*transition: 0s !important;*/
}

.MeteorToys_throttle_disabled, .MeteorToys_throttle_enabled1, .MeteorToys_throttle_enabled2 {
	background-image: url('data:image/png;base64,');
}
.MeteorToys_throttle_enabled1, .MeteorToys_throttle_enabled2 {
	background-image: url('data:image/png;base64,')
}
.MeteorToys_throttle_enabled2 #MeteorToys_indicator {
	-moz-transform: rotate(-13deg);
	-webkit-transform: rotate(-13deg);
	transform: rotate(-13deg);
}
.MeteorToys_throttle_enabled1 #MeteorToys_indicator { 
	/*background-image: url('./icon.png');*/
	-moz-transform: rotate(47deg);
	-webkit-transform: rotate(47deg);
	transform: rotate(47deg);
}

#MeteorToys_indicator {
	height: 46px;
	width: 46px;
	position: absolute;
	top: 0;
	left: 0;
	-moz-transform: rotate(106deg);
	-webkit-transform: rotate(106deg);
	transform: rotate(106deg);
	-webkit-transition: .3s;
	-moz-transition: .3s;
	transition: .3s;
	background-image: url('data:image/png;base64,');
	background-size: cover;

}