#JetSetter {
  right: 0;
  bottom: 0;
  overflow: auto; 
  max-height: 100%;
  max-width: 320px !important;
  cursor: default;
  box-sizing: border-box;
}

.JetSetter_row, .JetSetter_row_empty {
  margin-bottom: 1px;
  padding: 0 7px;
  overflow: hidden;
  white-space: nowrap;
  max-width: 140px;
  min-width: 140px;
  height: 28px;
  cursor: pointer;
  box-sizing: border-box;
  -webkit-transition: all .25s;
  -moz-transition:    all .25s;
  -ms-transition:     all .25s;
  -o-transition:      all .25s;
  transition:         all .25s;

}

.JetSetter_value { 
  -webkit-transition: all .25s;
  -moz-transition:    all .25s;
  -ms-transition:     all .25s;
  -o-transition:      all .25s;
  transition:         all .25s;
}
.JetSetter_row:last-child, .JetSetter_row_empty:last-child {
  margin-bottom: 0px;
}
.JetSetter_row {
  line-height: 28px;
}
.JetSetter_previewMode .JetSetter_row {
  max-width: 320px;
  width: 320px;
}
.JetSetter_expand .JetSetter_row {
  min-width: 320px;
  max-width: 320px;

 }
.JetSetter_expand {
  z-index: 2147483647;
  
}
.JetSetter_row_expand .JetSetter_value {
  opacity: 0 !important;
  
}
.JetSetter_row_expand { 
  height: 320px;
  cursor: default;
}
.JetSetter_row_expand .JetSetter_value_preview {
  opacity: 0 !important;
}

.JetSetter_editor {
  border-radius: 5px;
  height: calc(100% - 35px);
  /* Moved to ToyKit: box-shadow: 0 0 0 50px rgba(0,0,0,.3);*/
  overflow: hidden;
  width: 100%;

}

.JetSetter_row:active .JetSetter_editor {
  -webkit-transition: all .25s;
  -moz-transition:    all .25s;
  -ms-transition:     all .25s;
  -o-transition:      all .25s;
  transition:         all .25s;
}

/*.JetSetter_row:hover .JetSetter_editor {*/
  /*box-shadow: 0 0 0 50px rgba(0,0,0,.68); */
/*}*/

.JetSetter_row_expand .JetSetter_editor {
  /* Moved to ToyKit: box-shadow: 0 0 0 50px rgba(0,0,0,.68); */
}

.JetSetter_editor_header {
  background: rgba(0,0,0,.3);
  padding-left: 7px;

}

.JetSetter_editor_button {
  float: right;
  padding: 0 7px;
  border-left: 1px solid rgba(0,0,0,.3);
  cursor: pointer;
}
.JetSetter_editor_button:hover { 
  /*color: #00A8E0;*/
}

.JetSetter_editor_content {
  height: calc(100% - 31px);
  overflow: auto;
  padding: 0 7px;
  padding-top: 5px;
  line-height: 20px !important;
  word-wrap: none;
  outline: none;
  /*color: rgba(255,255,255,.6);*/
  text-align: left;
}

.JetSetter_editor_content_editing span { 

  line-height: 20px !important;
  /*color: rgba(255,255,255,.6) !important;*/
}
.JetSetter_fader { 
  /*color: rgba(255,255,255,.24);*/
  -webkit-transition: all .25s;
  -moz-transition:    all .25s;
  -ms-transition:     all .25s;
  -o-transition:      all .25s;
  transition:         all .25s;
}


.JetSetter_value { 
  /*color: rgba(255,255,255,.2) !important;*/
  text-overflow: ellipsis;
  -webkit-transition: all .25s;
  -moz-transition:    all .25s;
  -ms-transition:     all .25s;
  -o-transition:      all .25s;
  transition:         all .25s;
}

.JetSetter_editor_title { 
  /* 
    Just so CSS framework form overwrites 
    don't conflict with this one
  */
  background: transparent !important;
  color: inherit !important;
  font-size: inherit !important;
  font-family: inherit !important;
  line-height: inherit !important;
  height: 28px !important;
  border: 0px solid transparent !important;
  padding: 0px !important;
  margin: 0px 1px !important;
  /*color: #ccc !important;*/
  text-indent: 0px !important;
  line-height: 20px !important;
  float: left !important;
  max-width: 80% !important;
}



.JetSetter_dictTitle {
  white-space: nowrap;
  overflow: hidden;
  float: left;
  min-width: calc(100% + 14px);
  margin: 0 -7px;
  padding: 0 7px;
  line-height: 28px;


}

.JetSetter_dictTitle div {
  float: right;
}

.JetSetter_editor_header, .JetSetter_editor_header div { 
  line-height: 28px;
}

.JetSetter_editor_content_editing, .JetSetter_editor_content_editing span {
  color: inherit !important;
}